import {mapActions, mapState} from 'vuex'

export default {
    name: 'myOrderList',
    components: {},
    data () {
        return {}
    },
    computed: {
        ...mapState({
            orderList: state => state.order.orderList
        })
    },
    async created () {
        // 获取订单列表
        // await this.getOrderListFn();
    },
    methods: {
        ...mapActions([
            // 获取订单列表
            'getOrderList'
        ]),
        /**
         * 获取订单列表
         * @returns {Promise<void>}
         */
        async getOrderListFn () {
            // 订单类型
            let params = {type: ''}
            // 获取订单列表
            await this.getOrderList(params)
        },
        /**
         * 路由跳转方法
         * @param link
         */
        routeLink (link) {
            this.$router.push({path: link})
        }
    }
}



